import { dbService, storageService } from "fbase";
import React from "react";
import { useState } from "react";

import 'Kiwitts.css';
import 'KiwittForm.css';

const Kiwitt = ({ kiwittObj, isOwner }) => {
    const [editing, setEditing] = useState(false);
    const [newKiwitt, setNewKiwitt] = useState(kiwittObj.text);
    const onDeleteClick = async () => {
        const ok = window.confirm("Are you Sure?");
        if(ok) {
            await dbService.doc(`kiwitts/${kiwittObj.id}`).delete();
            await storageService.refFromURL(kiwittObj.attUrl).delete();
        }
    };
    const toggleEditing = () => setEditing((prev) => !prev);
    const onSubmit = async (event) => {
        event.preventDefault();
        await dbService.doc(`kiwitts/${kiwittObj.id}`).update({
            text: newKiwitt,
        });
        setEditing(false);
    };
    const onChange = (event) => {
        const {
            target: {value},
        } = event;
        setNewKiwitt(value);
    };
    return (
        <div>
            {editing ? (
                <>
                    <form className="KiwittEditForm" onSubmit={onSubmit}>
                        <div className="Text">
                            <input
                                className="KiwitMessage"
                                type="text"
                                placeholder="Edit your kiwitt"
                                value={newKiwitt}
                                required
                                onChange={onChange}
                            />
                        </div>
                        <div className="EditBt">
                            <input className="Update_b" type="submit" value="Update" />
                            <button className="Cancel_b" onClick={toggleEditing}>Cancel</button>
                        </div>
                    </form>
                </>
            ) : (
                <div className="KiwittContainer">
                    <div className="KiwittInfo">
                        {kiwittObj.attUrl && (
                            <img className="KiwittImg" src={kiwittObj.attUrl}/>
                        )}
                        <div className="KiwittText">{kiwittObj.text}</div>
                    </div>
                    <div className="KiwitterName">{kiwittObj.creatorName}ㆍ
                    {new Date(kiwittObj.createdAt).getMonth()}/{new Date(kiwittObj.createdAt).getDate()} - {new Date(kiwittObj.createdAt).getHours()}:
                    {new Date(kiwittObj.createdAt).getMinutes()}:
                    {new Date(kiwittObj.createdAt).getSeconds()}
                    </div>
                    {isOwner && (
                        <div className="KiwittBt">
                            <button className="KiwittBt_Delete" onClick={onDeleteClick}>
                                <svg class="KiwittBtIcon" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="trash" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512">
                                    <path fill="currentColor" d="M432 32H312l-9.4-18.7A24 24 0 0 0 281.1 0H166.8a23.72 23.72 0 0 0-21.4 13.3L136 32H16A16 16 0 0 0 0 48v32a16 16 0 0 0 16 16h416a16 16 0 0 0 16-16V48a16 16 0 0 0-16-16zM53.2 467a48 48 0 0 0 47.9 45h245.8a48 48 0 0 0 47.9-45L416 128H32z"></path>
                                </svg>
                            </button>
                            <button className="KiwittBt_Edit" onClick={toggleEditing}>
                                <svg class="KiwittBtIcon" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="pencil-alt" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                                    <path fill="currentColor" d="M497.9 142.1l-46.1 46.1c-4.7 4.7-12.3 4.7-17 0l-111-111c-4.7-4.7-4.7-12.3 0-17l46.1-46.1c18.7-18.7 49.1-18.7 67.9 0l60.1 60.1c18.8 18.7 18.8 49.1 0 67.9zM284.2 99.8L21.6 362.4.4 483.9c-2.9 16.4 11.4 30.6 27.8 27.8l121.5-21.3 262.6-262.6c4.7-4.7 4.7-12.3 0-17l-111-111c-4.8-4.7-12.4-4.7-17.1 0zM124.1 339.9c-5.5-5.5-5.5-14.3 0-19.8l154-154c5.5-5.5 14.3-5.5 19.8 0s5.5 14.3 0 19.8l-154 154c-5.5 5.5-14.3 5.5-19.8 0zM88 424h48v36.3l-64.5 11.3-31.1-31.1L51.7 376H88v48z"></path>
                                </svg>
                            </button>
                        </div>
                    )} 
                </div>
            )}
        </div>
    );
};

export default Kiwitt;