import { authService } from "fbase";
import React from "react";
import { useState } from "react";
import 'Login.css';


const AuthForm = () =>{
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [newAccount, setNewAccount] = useState(true);
    const [error, setError] = useState("");

    const onSubmit = async (evnet) => {
        evnet.preventDefault();
        try{
            let data
            if(newAccount){
                //create Account
                data = await authService.createUserWithEmailAndPassword( email, password );
            }
            else{
                //login
                data = await authService.signInWithEmailAndPassword( email, password );
            }
            console.log(data)
        }
        catch(error){
            setError(error.message);
        }
    };

    const toggleAccount = () => setNewAccount((prev) => !prev);

    const onChange = (event) => {
        const {
            target: {name, value},
        } = event;
        if(name === "email"){
            setEmail(value);
        }
        else if(name === "password"){
            setPassword(value);
        }
    };

    return (
        <>
            <div className="subtitle">{newAccount ? "Create Account" : "Sign up"}</div>
            <form onSubmit={onSubmit} className="container">
                <input
                    name="email"
                    type="text"
                    placeholder="Email"
                    required
                    value={email}
                    onChange={onChange}
                    className="AuthInput"
                />
                <input
                    name="password"
                    type="password"
                    placeholder="Password"
                    required 
                    value={password}
                    onChange={onChange}
                    className="AuthInput"
                />
                <input type="submit" value={newAccount ? "Create Account" : "Log In"} className="AuthSubmit" />
                <div className="AuthError">{error}</div>
            </form>
            <span className="AuthSwitch" onClick={toggleAccount}>{newAccount ? "Sign in" : "Create Account"}</span>
        </>
    );
};
export default AuthForm;