import { authService, dbService } from "fbase";
import { useHistory } from "react-router";
import { useState, useEffect } from "react";
import Kiwitt from "components/Kiwitt";

import 'Profile.css';

const Profile = ({ refreshUser, userObj }) => {
    const [ProfileEditing, setProfileEditing] = useState(false);
    const [myKiwitts, setMyKiwitts] = useState([]);
    const history = useHistory();
    const [newDisplayName, setNewDisplayName] = useState(userObj.displayName);
    const onLogOutClick = () => {
        authService.signOut();
        history.push("/");
        refreshUser();
    };
    const onChange = (event) => {
        const {
            target: {value},
        } = event;
        setNewDisplayName(value);
    }
    const onSubmit = async (event) => {
        event.preventDefault();
        if(userObj.displayName !== newDisplayName) {
            await userObj.updateProfile({
                displayName: newDisplayName,
            });
            refreshUser();
            toggleProfileEditing();
        }
    }

    const toggleProfileEditing = () => setProfileEditing((prev) => !prev);

    useEffect(() => {
        dbService.collection("kiwitts").where("creatorId", "==", userObj.uid).orderBy("createdAt", "desc").onSnapshot((snapshot) => {
            const kiwittArray = snapshot.docs.map((doc) => ({
                id:doc.id,
                ...doc.data(),
            }));
            setMyKiwitts(kiwittArray);
        });
    }, [])

    return (
        <div className="ProfileContainer">
            {ProfileEditing ? (
                <>
                    <form onSubmit={onSubmit} className="ProfileForm">
                        <input
                            className="ProfileFormNewname"
                            onChange={onChange} 
                            type="text" 
                            placeholder="Display name" 
                            value={newDisplayName}
                        />
                        <div className="EditBt">
                            <input className="ProfileFormUpdate" type="submit" value="Update" />
                            <button className="ProfileFormCancel" onClick={toggleProfileEditing}>Cancel</button>
                        </div>
                    </form>
                </>
            ) : (
                <div className="Profile">
                    <div className="ProfileName">
                        {userObj?.displayName?.length ? (
                            `${userObj.displayName} 님의 Profile`
                        ) : (
                            "User 님의 Profile"
                        )}
                    </div>
                    <button className="EditProfile" onClick={toggleProfileEditing}>
                        <svg class="KiwittBtIcon" aria-hidden="true" focusable="false" data-prefix="fas" data-icon="pencil-alt" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                            <path fill="currentColor" d="M497.9 142.1l-46.1 46.1c-4.7 4.7-12.3 4.7-17 0l-111-111c-4.7-4.7-4.7-12.3 0-17l46.1-46.1c18.7-18.7 49.1-18.7 67.9 0l60.1 60.1c18.8 18.7 18.8 49.1 0 67.9zM284.2 99.8L21.6 362.4.4 483.9c-2.9 16.4 11.4 30.6 27.8 27.8l121.5-21.3 262.6-262.6c4.7-4.7 4.7-12.3 0-17l-111-111c-4.8-4.7-12.4-4.7-17.1 0zM124.1 339.9c-5.5-5.5-5.5-14.3 0-19.8l154-154c5.5-5.5 14.3-5.5 19.8 0s5.5 14.3 0 19.8l-154 154c-5.5 5.5-14.3 5.5-19.8 0zM88 424h48v36.3l-64.5 11.3-31.1-31.1L51.7 376H88v48z"></path>
                        </svg>
                    </button>
                </div>
            )}
            <div className="MyKiwitt">
                    <div className="MyKiwitt_t">나의 KIWITT</div>
            </div>
            <div>
                {myKiwitts.map((kiwitt) => (
                        <Kiwitt Key={kiwitt.id} kiwittObj={kiwitt} isOwner={false}/>
                ))}
            </div>
            <button className="LogoutBt" onClick={onLogOutClick}>Log Out</button>
        </div>
    );
};
export default Profile;