import Kiwitt from "components/Kiwitt";
import { dbService } from "fbase";
import React, { useEffect, useState } from "react";
import KiwittFactory from "components/KiwittFactory";
import 'KiwittForm.css';

const Home= ({ userObj }) => {
    const [kiwitts, setKiwitts] = useState([]);
    useEffect(() => {
        dbService.collection("kiwitts").orderBy("createdAt", "desc").onSnapshot((snapshot) => {
            const kiwittArray = snapshot.docs.map((doc) => ({
                id:doc.id,
                ...doc.data(),
            }));
            setKiwitts(kiwittArray);
        });
    }, [])
    
    return (
        <div className="KiwittsContainer"> 
            <KiwittFactory userObj={userObj} />
            <div>
                {kiwitts.map((kiwitt) => (
                    <Kiwitt Key={kiwitt.id} kiwittObj={kiwitt} isOwner={kiwitt.creatorId === userObj.uid}/>
                ))}
            </div>
        </div>
    )
};

export default Home;