import { dbService, storageService } from "fbase";
import React from "react";
import {v4 as uuidv4 } from "uuid";
import { useState } from "react";
import 'KiwittForm.css'

const KiwittFactory = ({userObj}) => {
    const [kiwitt, setKiwitt] = useState("");
    const [Att, setAtt] = useState("");

    const onSubmit = async (event) => {
        event.preventDefault();
        let attUrl = "";
        if(Att !== ""){
            const attRef = storageService.ref().child(`${userObj.uid}/${uuidv4()}`);
            const response = await attRef.putString(Att, "data_url");
            attUrl = await response.ref.getDownloadURL();
        }
        const kiwittObj = {
            text: kiwitt,
            createdAt: Date.now(),
            creatorId: userObj.uid,
            attUrl,
            creatorName: userObj?.displayName?.length ? (
                `${userObj.displayName}`
            ) : (
                "User"
            )
        }
        dbService.collection("kiwitts").add(kiwittObj);
        setKiwitt("");
        setAtt("");
    };

    const onChange = (event) => {
        const {
            target:{value}, 
        } = event;
        setKiwitt(value)
    };

    const onFileChange = (event) => {
        const {
            target: { files },
        } = event;
        const theFile = files[0];
        const reader = new FileReader();
        reader.onloadend = (finishedEvent) => {
            const {
                currentTarget: {result},
            } = finishedEvent;
            setAtt(result);
        };
        reader.readAsDataURL(theFile);
    };

    const onClaerPhoto = () => setAtt(null);

    return (
        <form onSubmit={onSubmit} className="KiwittForm">
            <div className="Texts">
                <input 
                    id="kiwitt_"
                    className="KiwitMessage"
                    aria-hidden="true"
                    value={kiwitt}
                    onChange={onChange}
                    placeholder="어떤 생각을 하고 계신가요?"
                    maxLength={120}
                    required
                />
                <input className="Kiwitt_b" type="submit" value="Kiwitt" />
            </div>
            {Att && (
                    <div className="ImgContainer">
                        <img src={Att} className="AddedImage" />
                        <button className="AddedImageBtt_Cancel" onClick={onClaerPhoto}>X</button>
                    </div>
                )}
            <div>
            </div>
            <div className="Buttons">
                <label for="attach_file" class="AddImage">
                    <span>Add Image</span>
                    <input id="attach_file" type="file" accept="image/*" onChange={onFileChange} />
                </label>
            </div>
        </form>
    );
};
export default KiwittFactory;